@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates&family=Open+Sans+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.checkout-page{width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:50px auto 0}.checkout-page .checkout-header{width:100%;padding:10px 0;display:flex;justify-content:space-between;border-bottom:1px solid darkgrey}.checkout-page .checkout-header .header-block{text-transform:capitalize;width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page .total{margin-top:30px;margin-left:auto;font-size:36px}@media screen and (max-width: 800px){.checkout-page{min-height:0;width:90%}.checkout-page .header-block{font-size:11px}.checkout-page .total{margin:auto;margin-top:40px}}

.checkout-item{width:100%;display:flex;min-height:100px;border-bottom:1px solid darkgrey;padding:15px 0;font-size:20px;align-items:center}.checkout-item .image-container{width:23%;padding-right:15px}.checkout-item .image-container img{width:100%;height:100%}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{width:23%}.checkout-item .quantity{display:flex}.checkout-item .quantity .arrow{cursor:pointer}.checkout-item .quantity .value{margin:0 10px}.checkout-item .remove-button{padding-left:12px;cursor:pointer}@media screen and (max-width: 800px){.checkout-item .name{font-size:13px}}

.cart-dropdown{border:1px solid black;background-color:white;position:absolute;height:460px;display:flex;flex-direction:column;padding:20px;top:90px;right:40px;width:360px;z-index:5}.cart-dropdown .empty-message{font-size:14px;margin:70px auto}.cart-dropdown .cart-items{height:340px;display:flex;flex-direction:column;overflow:scroll}.cart-dropdown button{margin-top:auto}@media screen and (max-width: 800px){.cart-dropdown{height:300px;right:-80px;width:230px}}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:34px;height:50px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.header{margin:auto;width:86%}.ui.menu{font-family:'Montserrat'}.ui.massive.menu{font-size:18px}.ui.buttons>.ui.dropdown:last-child .menu,.ui.menu .right.dropdown.item .menu,.ui.menu .right.menu .dropdown:last-child .menu{font-size:19px}@media screen and (max-width: 800px){.header{height:60px;margin-bottom:40px;padding:0;width:100%}}

.edit-container{margin:auto;margin-top:50px;width:80%}.edit-container .edit-button{text-align:center}.edit-container h2{text-align:center}.ui.menu:first-child{height:100px}

.homepage{align-items:center;display:flex;flex-direction:column;padding:10px 10px}.directory-menu{display:flex;flex-wrap:wrap;justify-content:space-between;width:100%}.menu-item{align-items:center;border:1px solid black;display:flex;height:240px;flex:1 1 auto;min-width:30%;justify-content:center;margin:0 7.5px 15px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .content{align-items:center;border:1px solid black;display:flex;height:90px;padding:0 25px;flex-direction:column;justify-content:center}.menu-item .content .title{color:#4a4a4a;font-weight:bold;margin-bottom:6px;font-size:22px}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}@media screen and (max-width: 800px){.directory-menu{margin-top:20px}}

:root{--primary-color: #FF9501;--dark-color: #004A60;--light-color: #f4f4f4;--danger-color: #dc3545;--success-color: #28a745}.alert{color:#FF9501;color:var(--primary-color);font-weight:bold;margin:1rem 0;opacity:0.9;padding:0.8rem}.alert-primary{color:#FF9501;color:var(--primary-color)}.alert-light{color:#f4f4f4;color:var(--light-color)}.alert-dark{color:#004A60;color:var(--dark-color)}.alert-danger{color:#dc3545;color:var(--danger-color)}.alert-success{color:#28a745;color:var(--success-color)}.alert-white{background:#fff;color:#333;border:#ccc solid 1px}

.menu-item{align-items:center;border:1px solid black;display:flex;height:230px;flex:1 1 auto;justify-content:center;min-width:30%;margin:0 7.5px 15px;overflow:hidden}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{transform:scale(1.1);transition:transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:0.9}.menu-item.large{height:240px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-size:cover;background-position:center}.menu-item .content{align-items:center;background-color:white;border:1px solid black;height:70px;padding:0 7px;display:flex;flex-direction:column;justify-content:center;opacity:0.7;position:absolute}.menu-item .content .title{font-weight:bold;margin:0 6px 0;font-size:18px;color:darkgreen}.menu-item .content .subtitle{font-weight:bold;font-size:16px}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}

.shop-page{padding:20px 60px}@media screen and (max-width: 800px){.shop-page{padding:0 0}}

.collection-item{width:22vw;display:flex;flex-direction:column;height:350px;align-items:center}.collection-item .image{border-radius:4px;width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collection-item:hover .image{opacity:0.8}.collection-item .collection-footer{display:flex;height:5%;justify-content:space-between;font-size:18px;margin-bottom:5px;width:100%}.collection-item .collection-footer .name{width:90%;margin-bottom:15px}.collection-item .collection-footer .price{width:10%}@media screen and (max-width: 800px){.collection-item{width:40vw}.collection-item .collection-footer{font-size:13px}.collection-item:hover .image{opacity:unset}}

.collection-page{display:flex;flex-direction:column}.collection-page .title{font-size:38px;margin:0 auto 30px}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px}.collection-page .items .collection-item{margin-bottom:30px}@media screen and (max-width: 800px){.collection-page .title{margin-top:30px}.collection-page .items{display:grid;grid-template-columns:1fr 1fr;grid-gap:15px}}

.collection-preview{display:flex;flex-direction:column;margin-bottom:30px}.collection-preview .title{font-size:28px;margin-bottom:25px}.collection-preview .preview{display:flex;justify-content:space-between}@media screen and (max-width: 800px){.collection-preview{align-items:center;margin-bottom:2px;margin-top:30px}.collection-preview .preview{display:grid;grid-template-columns:1fr 1fr;grid-gap:15px}}

.collection-overview{display:flex;flex-direction:column}

.login{margin:auto;text-align:center;width:35vw}@media screen and (max-width: 800px){.login{width:80%}}

.register{margin:auto;text-align:center;width:35vw}@media screen and (max-width: 800px){.register{margin-bottom:50px;width:80%}}

body {
	font-family: 'Montserrat Alternates', sans-serif;
	padding: 20px;
}

a {
	color: black;
	text-decoration: none;
}

@media screen and (max-width: 800px) {
	body {
		padding: 10px;
	}
}

