.checkout-page {
  width: 55%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 50px auto 0;

  .checkout-header {
    width: 100%;
    padding: 10px 0;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid darkgrey;

    .header-block {
      text-transform: capitalize;
      width: 23%;

      &:last-child {
        width: 8%;
      }
    }
  }

  .total {
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
  }
}

@media screen and (max-width: 800px) {
  .checkout-page {
    min-height: 0;
    width: 90%;
  }

  .checkout-page .header-block{
    font-size: 11px;
  }

  .checkout-page .total {
    margin: auto;
    margin-top: 40px;
    
  }
}